import { formatDate } from '@angular/common';
import {
  AfterContentInit,
  AfterViewInit,
  ChangeDetectionStrategy,
  Component,
  ContentChildren,
  ElementRef,
  EventEmitter,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  Renderer2,
  TemplateRef,
} from '@angular/core';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { AdvancedCellComponent } from './advanced-cell.component';
import { AdvancedFilterComponent } from './advanced-filter.component';
import { Column, PageParams } from './advanced-table.module';

@Component({
  selector: 'advanced-table',
  templateUrl: './advanced-table.component.html',
  styleUrls: ['./advanced-table.component.less'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AdvancedTableComponent implements OnChanges, OnInit, AfterViewInit, AfterContentInit, OnDestroy {
  @Input() columns: Column[] = []; // åæ°æ®
  @Input() data: { content: object[]; totalElements: number } = { content: [], totalElements: 0 }; // è¡¨æ ¼æ°æ®
  @Input() selections: object[] = []; // å·²éé¡¹
  @Input() scroll: { x?: string | null; y?: string | null }; // åºå®è¡¨å¤´ï¼æ»å¨
  @Input() loading = false; // è¡¨æ ¼loading
  @Input() pageSize = 10; // æ¾ç¤ºæ¡æ°
  @Input() frontPagination = false; // æ¯å¦åç«¯åé¡µ
  @Input() showPagination = true; // æ¯å¦æ¾ç¤ºåé¡µå¨
  @Input() fixedPagination = true; // æ¯å¦åºå®åé¡µå¨
  @Input() showSizeChanger = true; // æ¯å¦æ¾ç¤ºæ¡æ°åæ¢å¨
  @Input() size = 'middle'; // è¡¨æ ¼size
  @Input() pageSizeOptions = [10, 30, 50, 100]; // é¡µæ°éæ©å¨å¯éå¼
  @Input() showCheckbox = false; // æ¯å¦æ¾ç¤ºå¤éæ¡
  @Input() showTitle = true; // æ¯å¦ætitle
  @Input() titleTemplate: TemplateRef<void>; // titleæ¨¡æ¿
  @Input() filterMultiple: string;

  @Output() columnsChange: EventEmitter<Column[]> = new EventEmitter(); // åæ°æ®æ¹åäºä»¶ ç¨äºååç»å®
  @Output() selectionsChange: EventEmitter<object[]> = new EventEmitter(); // å·²éé¡¹æ¹åäºä»¶ ç¨äºååç»å®
  @Output() load: EventEmitter<[PageParams, { [key: string]: any }?]> = new EventEmitter(); // loadäºä»¶
  @Output() sort: EventEmitter<{ key: string; value: 'descend' | 'ascend' | null }> = new EventEmitter(); // æåºäºä»¶
  @Output() linkClick: EventEmitter<{ field: string; rowData: any }> = new EventEmitter(); // é¾æ¥ç¹å»äºä»¶

  @ContentChildren(AdvancedCellComponent) customCells: AdvancedCellComponent[]; // èªå®ä¹ååæ ¼
  @ContentChildren(AdvancedFilterComponent) customFilters: AdvancedFilterComponent[]; // èªå®ä¹æç´¢ç»ä»¶

  load$: Subject<any> = new Subject(); // loadæµ

  displayData: any[] = []; // å½åæ¾ç¤ºæ°æ®
  pageIndex = 1; // å½åé¡µç 
  queryParams: { [key: string]: any } = {};
  sortParams: { key: string; value: 'descend' | 'ascend' | null };

  constructor(private _elementRef: ElementRef, private _renderer2: Renderer2) {}

  ngOnChanges(changes) {
    if (changes.data) {
      // éèµ°sort
      if (this.sortParams && this.sortParams.key && this.sortParams.value) {
        this.onSort(this.sortParams);
      }
    }
    if (changes.columns) {
      // æºè½è¯å¸
      changes.columns.currentValue.forEach(column => {
        if (column.showFilter && column.filterType === 'select' && Array.isArray(column.filterOptions)) {
          column.lexicon = column.lexicon ? [...column.lexicon, ...column.filterOptions] : column.filterOptions;
        }
      });
    }
  }

  ngOnInit() {
    /* tslint:disable */
    this.load$.pipe(debounceTime(20)).subscribe(() => {
      // æ¸ç©ºselections
      this.selections = [];
      this.selectionsChange.emit(this.selections);
      // ååºloadäºä»¶
      this.load.emit([{ page: this.pageIndex, size: this.pageSize }, this.queryParams]);
    });
  }

  ngAfterViewInit() {
    // é¡µé¢åå§åå®æåèªå¨loadä¸æ¬¡
    this.load$.next();
    if (this.fixedPagination) {
      this.toFixedPagination();
    }
  }

  ngAfterContentInit() {
    // èµå¼èªå®ä¹ååæ ¼
    this.customCells.forEach(customCell => {
      const findedColumn = this.columns.find(column => column.field === customCell.field);
      if (findedColumn) findedColumn.customCell = customCell.templateRef;
    });
    // èµå¼èªå®ä¹æç´¢ç»ä»¶
    this.customFilters.forEach(customFilter => {
      const findedColumn = this.columns.find(column => column.field === customFilter.field);
      if (findedColumn) {
        findedColumn.showFilter = true;
        findedColumn.customFilter = customFilter.templateRef;
      }
    });
  }

  ngOnDestroy() {
    // ä¸ä½¿ç¨takeUntilæ¯å ä¸ºç´æ¥unsubscribeæ§è½æ´å¥½
    this.load$.unsubscribe();
  }

  /**
   * è¡¨æ ¼å½åæ¾ç¤ºæ°æ®æ¹åæ¶è°ç¨
   * @param currentData å½åé¡µæ¾ç¤ºæ°æ®
   */
  currentPageDataChange(currentData: any[]): void {
    this.displayData = currentData;
  }

  /**
   * åéæ¹ååè°
   */
  singleCheckChange(): void {
    this.updateSelections();
  }

  /**
   * å¨éå¤éæ¡æ¹ååè°
   * @param isChecked æ¯å¦å¨é
   */
  allCheckChange(isChecked: boolean): void {
    this.displayData.forEach(row => (row.isChecked = isChecked));
    this.updateSelections();
  }

  /**
   * æ´æ°selections
   */
  updateSelections(): void {
    this.selections = this.displayData.filter(row => row.isChecked);
    this.selectionsChange.emit(this.selections);
  }

  /**
   * é¡µç æ¹ååè°
   */
  pageIndexChange(): void {
    this.load$.next();
  }

  /**
   * æ¾ç¤ºæ¡æ°æ¹ååè°
   */
  pageSizeChange(): void {
    // æ¾ç¤ºæ¡æ°æ¹åæ¶åå°é¦é¡µ
    this.pageIndex = 1;
    this.load$.next();
  }

  /**
   * æåºæ¹å
   * @param sortParams æåºåæ°
   */
  onSort(sortParams: { key: string; value: 'descend' | 'ascend' | null }): void {
    // ä¿å­æåºåæ°ï¼ç¨äºä¸æ¬¡æ°æ®è¿æ¥åè¿è¡æåº
    this.sortParams = sortParams;
    // æ¥æ¾æ­£å¨æåºçå
    const sortColumn = this.columns.find(column => column.field === sortParams.key);

    // å¦ææ²¡æèªå®ä¹æåºï¼èªå¨åç«¯æåº
    if (sortColumn && !sortColumn.customSort) {
      this.data.content.sort((previous, further) =>
        sortParams.value === 'descend'
          ? further[sortParams.key] > previous[sortParams.key]
            ? 1
            : -1
          : previous[sortParams.key] > further[sortParams.key]
          ? 1
          : -1,
      );
      this.data.content = [...this.data.content];
    }
    this.sort.emit(sortParams);
  }

  /**
   * æ¥ææ¹ååè°
   * @param isOpen æ¯å¦æå¼
   * @param column å½ååæ¨¡åæ°æ®
   */
  onRangePickerOpenChange(isOpen: boolean, column: Column): void {
    if (isOpen === false) {
      const date = column.searchValue;
      if (date && Array.isArray(date) && date.length === 2) {
        column.searchValue = [formatDate(date[0], 'yyyy-MM-dd 00:00:00', 'zh_CN'), formatDate(date[1], 'yyyy-MM-dd 23:59:59', 'zh_CN')];
        column.displayValue = [formatDate(date[0], 'yyyy-MM-dd', 'zh_CN'), formatDate(date[1], 'yyyy-MM-dd', 'zh_CN')];
      }
    }
  }

  /**
   * æ¥è¯¢ç¡®è®¤åè°
   */
  onFilterConfim(): void {
    this.columns = [...this.columns]; // ä¼ å¥å­ç»ä»¶query-display
    this.columnsChange.emit(this.columns); // ä¼ åºç¶ç»ä»¶
  }

  /**
   * æ¥è¯¢åæ°æ¹ååè°
   * @param queryParams æ¥è¯¢åæ°
   */
  onQueryChange(queryParams: { [key: string]: any }): void {
    this.queryParams = queryParams;
    this.load$.next();
  }

  /**
   * åºå®åé¡µ
   */
  toFixedPagination(): void {
    // zorroè®¾è®¡ç¼ºé·ï¼æ²¡ææ»å¨æ¡æ¶åææ»å¨æ¡æ¶tableBodyä¼ä¸ä¸æ ·ï¼æåç»ä¸æ»å¨æ¡
    this.scroll = { y: '0px' };
    // ç­å¾æ»å¨æ¡æ´æ°
    setTimeout(() => {
      const windowHeight = document.documentElement.clientHeight;
      const tableBody = this._elementRef.nativeElement.querySelector('.ant-table-body');
      const pagination = this._elementRef.nativeElement.querySelector('nz-pagination');
      const tableBodyTop = tableBody.getBoundingClientRect().top;
      const scrollHeight = windowHeight - tableBodyTop - pagination.clientHeight - 8 + 'px';
      this.scroll = { ...this.scroll, y: scrollHeight };
      this._renderer2.setStyle(tableBody, 'height', scrollHeight);
    });
  }

  onlinkClick(field: string, rowData: any) {
    this.linkClick.emit({ field, rowData });
  }
}
